import React, { useState, useEffect, useMemo } from 'react';
import { 
  ThemeProvider, 
  createTheme, 
  useMediaQuery,
  CssBaseline,
  Box,
  Container,
  Typography,
  Paper,
  Grid,
  Tabs,
  Tab,
  Button,
  TextField,
  MenuItem,
  FormControl,
  InputLabel,
  Select,
  Card,
  CardContent,
  IconButton,
  Tooltip as MuiTooltip
} from '@mui/material';
import Brightness4Icon from '@mui/icons-material/Brightness4';
import Brightness7Icon from '@mui/icons-material/Brightness7';
import { 
  Bar,
  Line,
  Pie,
  Doughnut
} from 'react-chartjs-2';
import {
  Chart as ChartJS, 
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  Title,
  Tooltip,
  Legend, 
  ArcElement 
} from 'chart.js';
import DataAnalysis from './components/DataAnalysis';
import Loader from './components/Loader';
import DataFilters from './components/DataFilters';
// import { hebrewNumerals } from './utils/hebrewUtils';
import transformedData from './data/realCountryData';
// import realData from './data/realData';

// Register ChartJS components
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  ArcElement,
  Title,
  Tooltip,
  Legend
);

function App() {
  // State for dark mode
  const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');
  const [mode, setMode] = useState(() => {
    const savedMode = localStorage.getItem('themeMode');
    return savedMode || (prefersDarkMode ? 'dark' : 'light');
  });

  // Save theme preference to localStorage
  useEffect(() => {
    localStorage.setItem('themeMode', mode);
  }, [mode]);

  // Toggle theme mode
  const toggleThemeMode = () => {
    setMode((prevMode) => (prevMode === 'light' ? 'dark' : 'light'));
  };

  // Create theme based on current mode
  const theme = useMemo(
    () =>
      createTheme({
        direction: 'rtl',
        palette: {
          mode,
          ...(mode === 'light'
            ? {
                // Light mode colors
                primary: {
                  main: '#1976d2',
                },
                secondary: {
                  main: '#dc004e',
                },
                background: {
                  default: '#f5f7fa',
                  paper: '#ffffff',
                },
              }
            : {
                // Dark mode colors
                primary: {
                  main: '#90caf9',
                },
                secondary: {
                  main: '#f48fb1',
                },
                background: {
                  default: '#121212',
                  paper: '#1e1e1e',
                },
                text: {
                  primary: '#ffffff',
                  secondary: '#b0b0b0',
                },
              }),
        },
        typography: {
          fontFamily: [
            'Roboto',
            'Arial',
            'sans-serif'
          ].join(','),
        },
      }),
    [mode]
  );

  // State for data and filters
  const [data, setData] = useState([]);
  const [filteredData, setFilteredData] = useState([]);
  const [filters, setFilters] = useState({
    month: '',
    country: ''
  });
  
  // Filter options
  const [monthOptions, setMonthOptions] = useState([]);
  const [monthDisplayNames, setMonthDisplayNames] = useState({
    '01': 'ינואר',
    '02': 'פברואר',
    '03': 'מרץ',
    '04': 'אפריל',
    '05': 'מאי',
    '06': 'יוני',
    '07': 'יולי',
    '08': 'אוגוסט',
    '09': 'ספטמבר',
    '10': 'אוקטובר',
    '11': 'נובמבר',
    '12': 'דצמבר'
  });
  const [countryOptions, setCountryOptions] = useState([]);
  
  // State for statistics
  const [stats, setStats] = useState({
    totalImports: 0,
    averageValue: 0,
    topCountry: '',
    yearlyGrowth: 0,
    totalCountries: 0,
    totalQuantity: 0
  });
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(true);
  const [forceLoading, setForceLoading] = useState(true);
  const [tabValue, setTabValue] = useState(0);

  // Handle filter changes
  const handleFilterChange = (name, value) => {
    const newFilters = { ...filters, [name]: value };
    setFilters(newFilters);
    
    // Apply filters to data
    let filtered = data;
    
    if (newFilters.month) {
      filtered = filtered.filter(item => item.month === newFilters.month);
    }
    
    if (newFilters.country) {
      filtered = filtered.filter(item => item.country === newFilters.country);
    }
    
    setFilteredData(filtered);
    calculateStats(filtered);
  };

  // Reset filters
  const handleResetFilters = () => {
    setFilters({
      month: '',
      country: ''
    });
    setFilteredData(data);
    calculateStats(data);
  };

  // Calculate statistics from data
  const calculateStats = (data) => {
    // Total imports value
    const totalImports = data.reduce((sum, item) => sum + parseFloat(item.value || 0), 0);
    
    // Average value per import
    const averageValue = totalImports / data.length;
    
    // Top importing country
    const countryTotals = data.reduce((acc, item) => {
      const country = item.country;
      const value = parseFloat(item.value || 0);
      acc[country] = (acc[country] || 0) + value;
      return acc;
    }, {});
    
    const topCountry = Object.entries(countryTotals)
      .sort((a, b) => b[1] - a[1])
      .map(entry => entry[0])[0];
    
    // Total number of unique countries
    const totalCountries = Object.keys(countryTotals).length;
    
    // Total quantity of all items
    const totalQuantity = data.reduce((sum, item) => sum + parseFloat(item.quantity || 0), 0);
    
    // Monthly growth rate (comparing with previous month)
    let monthlyGrowth = 0;
    
    if (data.length > 0) {
      // Get all year-month combinations
      const yearMonths = [...new Set(data.map(item => `${item.year}-${item.month}`))];
      yearMonths.sort(); // Sort chronologically
      
      if (yearMonths.length >= 2) {
        const latestYearMonth = yearMonths[yearMonths.length - 1];
        const previousYearMonth = yearMonths[yearMonths.length - 2];
        
        const [latestYear, latestMonth] = latestYearMonth.split('-');
        const [previousYear, previousMonth] = previousYearMonth.split('-');
        
        const latestMonthTotal = data
          .filter(item => item.year === latestYear && item.month === latestMonth)
          .reduce((sum, item) => sum + parseFloat(item.value || 0), 0);
        
        const previousMonthTotal = data
          .filter(item => item.year === previousYear && item.month === previousMonth)
          .reduce((sum, item) => sum + parseFloat(item.value || 0), 0);
        
        if (previousMonthTotal > 0 && latestMonth !== '01') { // Skip comparison for January (first month)
          monthlyGrowth = ((latestMonthTotal - previousMonthTotal) / previousMonthTotal) * 100;
        }
      }
    }
    
    setStats({
      totalImports: totalImports.toFixed(2),
      averageValue: averageValue.toFixed(2),
      topCountry,
      yearlyGrowth: monthlyGrowth.toFixed(2), // Keep the same property name for compatibility
      totalCountries,
      totalQuantity: totalQuantity.toFixed(0)
    });
  };

  // Load real data
  useEffect(() => {
    // Set loading state
    setLoading(true);
    setForceLoading(true);
    
    // Simulate API call with timeout
    setTimeout(() => {
      try {
        // Process the data
        const parsedData = transformedData;
        
        // Set data state
        setData(parsedData);
        setFilteredData(parsedData);
        
        // Extract unique values for filters
        const months = [...new Set(parsedData.map(item => item.month))];
        const countries = [...new Set(parsedData.map(item => item.country))];
        
        setMonthOptions(months.sort());
        setCountryOptions(countries.sort());
        
        // Calculate basic statistics
        calculateStats(parsedData);
        
        setLoading(false);
      } catch (error) {
        setError('Error loading data: ' + error.message);
        setLoading(false);
      }
    }, 1000);
    
    // Force loader to display for at least 5 seconds
    setTimeout(() => {
      setForceLoading(false);
    }, 5000);
  }, []);

  // Handle tab change
  const handleTabChange = (event, newValue) => {
    setTabValue(newValue);
  };

  // Prepare chart data for imports by country
  const importsByCountryData = {
    labels: countryOptions.slice(0, 10),
    datasets: [
      {
        label: 'ערך יבוא (במיליוני ₪)',
        data: countryOptions.slice(0, 10).map(country => {
          return filteredData
            .filter(item => item.country === country)
            .reduce((sum, item) => sum + parseFloat(item.value || 0), 0);
        }),
        backgroundColor: [
          '#4e79a7', '#f28e2c', '#e15759', '#76b7b2', '#59a14f',
          '#edc949', '#af7aa1', '#ff9da7', '#9c755f', '#bab0ab'
        ],
      },
    ],
  };

  // Prepare chart data for imports by month (2024 only)
  const importsByMonthData = {
    labels: ['ינואר', 'פברואר', 'מרץ', 'אפריל', 'מאי', 'יוני', 'יולי', 'אוגוסט', 'ספטמבר', 'אוקטובר', 'נובמבר', 'דצמבר'],
    datasets: [
      {
        label: 'ערך יבוא (במיליוני ₪)',
        data: ['01', '02', '03', '04', '05', '06', '07', '08', '09', '10', '11', '12'].map(month => {
          return filteredData
            .filter(item => item.year === '2024' && item.month === month)
            .reduce((sum, item) => sum + parseFloat(item.value || 0), 0) / 1000000; // Convert to millions
        }),
        borderColor: '#2e7d32',
        backgroundColor: 'rgba(46, 125, 50, 0.2)',
      },
    ],
  };

  if (loading || forceLoading) {
    return (
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <Loader />
      </ThemeProvider>
    );
  }

  if (error) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh' }}>
        <Typography color="error">{error}</Typography>
      </Box>
    );
  }

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Box sx={{ 
        bgcolor: 'background.default', 
        color: 'text.primary',
        minHeight: '100vh',
        transition: 'background-color 0.3s, color 0.3s'
      }}>
        <Container maxWidth="xl" className="dashboard-container">
          <Box sx={{ 
            display: 'flex', 
            justifyContent: 'space-between', 
            alignItems: 'center', 
            mb: 3,
            mt: 2,
            py: 2,
            px: 2,
            borderBottom: 1,
            borderColor: 'divider',
            position: 'relative'
          }}>
            {/* Logo on the right */}
            <Box sx={{ 
              display: 'flex', 
              alignItems: 'center',
              position: 'absolute',
              right: 16
            }}>
              <img 
                src={`${process.env.PUBLIC_URL}/pwc_logo.png`}
                alt="PWC Logo"
                style={{ 
                  width: '70px', 
                  height: '70px', 
                  objectFit: 'contain',
                  padding: '4px',
                  background: '#ffffff29'
                }}
              />
            </Box>
            
            {/* Title in the center */}
            <Box sx={{ 
              position: 'absolute', 
              left: '50%', 
              transform: 'translateX(-50%)',
              textAlign: 'center'
            }}>
              <Typography 
                variant="h4" 
                component="h1" 
                className="dashboard-title"
                sx={{ 
                  fontWeight: 700,
                  fontFamily: 'Rubik, sans-serif',
                  letterSpacing: '0.5px',
                  fontSize: '2.2rem'
                }}
              >
                דשבורד נתוני יבוא לישראל
              </Typography>
            </Box>
            
            {/* Theme toggle on the left */}
            <Box sx={{ 
              position: 'absolute',
              left: 16
            }}>
              <MuiTooltip title={mode === 'dark' ? 'מצב בהיר' : 'מצב כהה'}>
                <IconButton onClick={toggleThemeMode} color="inherit" size="large">
                  {mode === 'dark' ? <Brightness7Icon /> : <Brightness4Icon />}
                </IconButton>
              </MuiTooltip>
            </Box>
          </Box>

          {/* Statistics Cards */}
          <Grid container spacing={3} sx={{ mb: 4 }}>
            <Grid item xs={12} sm={6} md={4} lg={2.4}>
              <Card className="stat-card">
                <CardContent>
                  <Typography className="stat-title" color="textSecondary" gutterBottom>
                    סך הכל יבוא
                  </Typography>
                  <Typography className="stat-value" variant="h5" component="div">
                    ₪{Number(stats.totalImports).toLocaleString()}
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={12} sm={6} md={4} lg={2.4}>
              <Card className="stat-card">
                <CardContent>
                  <Typography className="stat-title" color="textSecondary" gutterBottom>
                    {filters.country ? "מדינת היבוא" : "מדינת יבוא מובילה"}
                  </Typography>
                  <Typography className="stat-value" variant="h5" component="div">
                    {filters.country || stats.topCountry}
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={12} sm={6} md={4} lg={2.4}>
              <Card className="stat-card">
                <CardContent>
                  <Typography className="stat-title" color="textSecondary" gutterBottom>
                    ערך ממוצע ליבוא
                  </Typography>
                  <Typography className="stat-value" variant="h5" component="div">
                    ₪{Number(stats.averageValue).toLocaleString()}
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={12} sm={6} md={4} lg={2.4}>
              <Card className="stat-card">
                <CardContent>
                  <Typography className="stat-title" color="textSecondary" gutterBottom>
                    מספר רשומות
                  </Typography>
                  <Typography className="stat-value" variant="h5" component="div">
                    {Number(stats.totalQuantity).toLocaleString()}
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={12} sm={6} md={4} lg={2.4}>
              <Card className="stat-card">
                <CardContent>
                  <Typography className="stat-title" color="textSecondary" gutterBottom>
                    מספר מדינות
                  </Typography>
                  <Typography className="stat-value" variant="h5" component="div">
                    {stats.totalCountries}
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
          </Grid>

          {/* Data Filters */}
          {!loading && !forceLoading && (
            <DataFilters 
              filters={filters} 
              options={{
                months: monthOptions.map(month => ({ value: month, label: monthDisplayNames[month] })),
                countries: countryOptions
              }}
              onChange={handleFilterChange}
              onReset={handleResetFilters}
            />
          )}

          {/* Tabs for different views */}
          <Paper elevation={3} className="dashboard-card">
            <Tabs 
              value={tabValue} 
              onChange={handleTabChange} 
              className="dashboard-tabs"
              variant="scrollable"
              scrollButtons="auto"
            >
              <Tab label="טבלת נתונים" />
              <Tab label="ניתוח נתונים" />
              <Tab label="גרפים" />
            </Tabs>

            {/* Tab content */}
            {tabValue === 0 && (
              <Box>
                <Typography variant="h6" gutterBottom>
                  נתוני יבוא
                </Typography>
                <Box sx={{ display: 'flex', justifyContent: 'center', width: '100%' }}>
                  <iframe 
                    title="Data viewer" 
                    width="100%" 
                    height="600px"
                    src="https://data.gov.il/dataset/customs_import_statistics_data/resource/6d3b03e1-de9c-42a8-bb08-91ba564c2f34/view/fc314eaf-9548-405d-91e5-d9778d8f38eb" 
                    frameBorder="0"
                  ></iframe>
                </Box>
              </Box>
            )}

            {tabValue === 1 && (
              <Box>
                <Typography variant="h6" gutterBottom>
                  ניתוח נתונים
                </Typography>
                <DataAnalysis data={filteredData} />
              </Box>
            )}

            {tabValue === 2 && (
              <Box>
                <Typography variant="h6" gutterBottom>
                  גרפים וויזואליזציות
                </Typography>
                <Grid container spacing={3}>
                  <Grid item xs={12} md={6}>
                    <Paper elevation={2} sx={{ p: 2 }}>
                      <Typography variant="subtitle1" gutterBottom>
                        יבוא לפי מדינה (10 המדינות המובילות)
                      </Typography>
                      <div className="chart-container">
                        <Bar 
                          data={importsByCountryData} 
                          options={{
                            responsive: true,
                            plugins: {
                              legend: {
                                position: 'top',
                              },
                              title: {
                                display: false,
                              },
                            },
                          }}
                        />
                      </div>
                    </Paper>
                  </Grid>
                  <Grid item xs={12} md={6}>
                    <Paper elevation={2} sx={{ p: 2 }}>
                      <Typography variant="subtitle1" gutterBottom>
                        יבוא לפי חודש (2024)
                      </Typography>
                      <div className="chart-container">
                        <Bar 
                          data={importsByMonthData} 
                          options={{
                            responsive: true,
                            plugins: {
                              legend: {
                                position: 'top',
                              },
                              title: {
                                display: false,
                              },
                            },
                          }}
                        />
                      </div>
                    </Paper>
                  </Grid>
                </Grid>
              </Box>
            )}
          </Paper>
        </Container>
      </Box>
    </ThemeProvider>
  );
}

export default App;
