import React from 'react';
import { 
  Grid, 
  Paper, 
  Typography, 
  Table, 
  TableBody, 
  TableCell, 
  TableContainer, 
  TableHead, 
  TableRow
} from '@mui/material';

function DataAnalysis({ data }) {
  // Calculate statistics by country
  const countryStats = React.useMemo(() => {
    const stats = {};
    
    data.forEach(item => {
      const country = item.country;
      const value = parseFloat(item.value || 0);
      const quantity = parseFloat(item.quantity || 0);
      
      if (!stats[country]) {
        stats[country] = {
          totalValue: 0,
          totalQuantity: 0,
          count: 0,
          average: 0
        };
      }
      
      stats[country].totalValue += value;
      stats[country].totalQuantity += quantity;
      stats[country].count += 1;
    });
    
    // Calculate averages and prepare for display
    const result = Object.entries(stats).map(([country, stat]) => ({
      country,
      totalValue: stat.totalValue,
      totalQuantity: stat.totalQuantity,
      count: stat.count,
      average: stat.totalValue / stat.count
    }));
    
    // Sort by total value descending
    return result.sort((a, b) => b.totalValue - a.totalValue);
  }, [data]);
  
  // Find outliers (simplified method - values more than 2 standard deviations from mean)
  const outliers = React.useMemo(() => {
    if (data.length === 0) return [];
    
    const values = data.map(item => parseFloat(item.value || 0));
    const mean = values.reduce((sum, val) => sum + val, 0) / values.length;
    
    const squaredDiffs = values.map(val => Math.pow(val - mean, 2));
    const variance = squaredDiffs.reduce((sum, val) => sum + val, 0) / values.length;
    const stdDev = Math.sqrt(variance);
    
    const threshold = 2 * stdDev;
    
    return data
      .filter(item => Math.abs(parseFloat(item.value || 0) - mean) > threshold)
      .sort((a, b) => parseFloat(b.value || 0) - parseFloat(a.value || 0))
      .slice(0, 10); // Show top 10 outliers
  }, [data]);

  return (
    <Grid container spacing={3}>
      <Grid item xs={12} md={6}>
        <Paper elevation={2} sx={{ p: 2 }}>
          <Typography variant="h6" gutterBottom>
            ניתוח לפי מדינה (20 מדינות מובילות)
          </Typography>
          <TableContainer>
            <Table size="small">
              <TableHead>
                <TableRow>
                  <TableCell>מדינה</TableCell>
                  <TableCell align="right">סך הכל ערך (₪)</TableCell>
                  <TableCell align="right">סך הכל כמות</TableCell>
                  <TableCell align="right">ממוצע (₪)</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {countryStats.slice(0, 20).map((row) => (
                  <TableRow key={row.country}>
                    <TableCell component="th" scope="row">
                      {row.country}
                    </TableCell>
                    <TableCell align="right">{Math.round(row.totalValue).toLocaleString()}</TableCell>
                    <TableCell align="right">{Math.round(row.totalQuantity).toLocaleString()}</TableCell>
                    <TableCell align="right">{Math.round(row.average).toLocaleString()}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Paper>
      </Grid>
      
      <Grid item xs={12}>
        <Paper elevation={2} sx={{ p: 2 }}>
          <Typography variant="h6" gutterBottom>
            חריגים משמעותיים בנתונים
          </Typography>
          <TableContainer>
            <Table size="small">
              <TableHead>
                <TableRow>
                  <TableCell>תאריך</TableCell>
                  <TableCell>מדינה</TableCell>
                  <TableCell>קטגוריה</TableCell>
                  <TableCell align="right">ערך (₪)</TableCell>
                  <TableCell>הערות</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {outliers.map((row, index) => (
                  <TableRow key={index}>
                    <TableCell>{row.date}</TableCell>
                    <TableCell>{row.country}</TableCell>
                    <TableCell>{row.category}</TableCell>
                    <TableCell align="right">{parseFloat(row.value).toLocaleString()}</TableCell>
                    <TableCell>{row.notes}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Paper>
      </Grid>
    </Grid>
  );
}

export default DataAnalysis;
