// Hebrew month names
const hebrewMonths = [
  'ינואר', 'פברואר', 'מרץ', 'אפריל', 'מאי', 'יוני',
  'יולי', 'אוגוסט', 'ספטמבר', 'אוקטובר', 'נובמבר', 'דצמבר'
];

// Transform the data into the format expected by the application
const transformedData = [];
let id = 1;

// Process each country in the JSON data
Object.entries(countryData).forEach(([countryCode, data]) => {
  const countryName = countryCodeToName[countryCode] || countryCode;
  
  // Process monthly data for 2024
  data.NISCurrencyAmount.forEach((value, index) => {
    if (index < 12) { // Ensure we only use the first 12 months (if there are more)
      const month = (index + 1).toString().padStart(2, '0');
      
      transformedData.push({
        id: id++,
        date: `2024-${month}-15`, // Using 15th of each month as a placeholder
        year: '2024',
        month: month,
        country: countryName,
        category: 'כללי', // General category as a placeholder
        value: value,
        quantity: data.Total[index],
        unit: 'יחידות',
        tax: Math.round(value * 0.15), // Placeholder tax calculation
        vat: Math.round(value * 0.17), // Placeholder VAT calculation
        notes: ''
      });
    }
  });
});

export default transformedData;
